:root {
    box-sizing: border-box;
    font-size: 80%;
    color: darkmagenta; }
  
  body {
    font-family: 'Raleway', sans-serif; }
  
  h1 {
    color: #0f0101;
    font-size: 1em;
    margin-bottom: 3em; }
  
  label {
    display: block;
    margin-bottom: 1rem;
    cursor: pointer; }
  
  input {
    border: 1px solid #2c97b8;
    padding: .5rem;
    width: 20rem;
    margin-bottom: 1rem;
    font-family: "Raleway", sans-serif; }
  
  .input_Desc {
    margin-bottom: 2rem;
    width: 30rem;
    height: 10rem; }
  
  button {
    border: none;
    background: darkgreen;
    color: white;
    padding: .5rem 2rem;
    cursor: pointer;
    margin-bottom: 2rem; }
  
  [class^='nuevo-proyecto'] {
